@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,300;0,400;0,600;0,700;1,400;1,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,h2,h3,h4,h5,h6,p{font-family:"Source Sans Pro",sans-serif;color:#4c4c47}body{background:#7ebdc2}.hero-wrapper{width:100%;height:90vh;display:flex;justify-content:center;align-items:center}.hero-wrapper .image-container{padding:10vw 0px;background-image:url(/static/media/two-young-people-training-on-cycling-machines-2MY8G3V.14332a06.jpg);background-size:cover;background-repeat:no-repeat;border-radius:.3vw;width:100vw;display:flex;justify-content:center;box-shadow:0px 0px 41px -9px rgba(0,0,0,.51);-webkit-box-shadow:0px 0px 41px -9px rgba(0,0,0,.51);-moz-box-shadow:0px 0px 41px -9px rgba(0,0,0,.51)}.countdown{width:70vw;background:#fff;background:linear-gradient(325deg, white 0%, rgba(255, 255, 255, 0.4) 100%);box-shadow:0px 0px 41px -9px rgba(0,0,0,.51);-webkit-box-shadow:0px 0px 41px -9px rgba(0,0,0,.51);-moz-box-shadow:0px 0px 41px -9px rgba(0,0,0,.51);padding:40px;border-radius:.3vw;-webkit-backdrop-filter:blur(3px);backdrop-filter:blur(3px);-webkit-transition:.5s cubic-bezier(0.075, 0.82, 0.165, 1);transition:.5s cubic-bezier(0.075, 0.82, 0.165, 1)}.countdown:hover{-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);box-shadow:0px 0px 55px -9px rgba(0,0,0,.7);-webkit-box-shadow:0px 0px 55px -9px rgba(0,0,0,.7);-moz-box-shadow:0px 0px 55px -9px rgba(0,0,0,.7)}.countdown h2{text-align:center}.countdown .countdown-item-wrapper{display:grid;grid-template-columns:1fr 1fr 1fr 1fr}.countdown .countdown-item-wrapper .countdown__box{display:flex;flex-direction:column;align-items:center}.countdown .countdown-item-wrapper .countdown__box_left{font-size:5vw;color:#bb4430}.countdown .countdown-item-wrapper .countdown__box p{text-transform:uppercase}.footer-wrapper{width:100%;height:100px;background:#231f20;position:absolute;bottom:0}
